.image-box {
    width: 400px;
    height: 400px;
    /* round colors */
    border-radius: 10px;
    position: relative;
    overflow: hidden;
}
p {
    position: absolute;
    margin: 0;
    margin-left: 10px;
    margin-top: 5px;
}
.box {
    position: absolute;
    height: 100%;
    width: 100%;
}